        // 服务定级结果
        dialogClassificationVisiable_target: false,
        dialogClassificationVisiable_family: false,
        dialogClassificationVisiable_sag: false,
        formServiceClassification_target: null,
        formServiceClassification_family: null,
        formServiceClassification_sag: null,
        // 对话框
        dialogDeleteVisiable: false,
      }
    },
    created () {
      // 从后台获取问题对象
      this.createServiceTable()
      this.getIndex()
    },
    mounted () {
      this.$refs.dialogDeleteData.close()
    },
    methods: {
      createServiceTable: function () {
        RetrieveServiceEvaluation()
          .then(async res => {
            const responseData = res.filter(e => e.deletestate == '1');
            this.serviceTable = responseData
          })
          .catch(err => {
            console.log('err: ', err)
          })
      },

      // 获取 index指标项全集
      getIndex: function() {
        GetIndexInfo()
          .then(async res => {
            const responseData = res;
            this.index_base = responseData
          })
          .catch(err => {
            console.log('err: ', err)
          })
      },

      addService: function(){
        this.formServiceInfo = {

        };
        this.dialogServiceInfoAddVisiable = true;
      },

      callDelete: function(currentRow) {
        this.dialogDeleteData = currentRow;
        setTimeout(() => {
          this.$refs.dialogDeleteData.open()
        }, 10)
      },

      callOperation: function(opt, para) {
        if (opt){
          DeleteServiceInfo(para).then(res => {
            if (res === "成功") {
              console.log("删除成功")
              this.reload()
            } else {
              alert("删除异常，请联系管理员。")
            }
          }).catch(err => {
            console.log('err: ', err)
          })
        }
        this.$refs.dialogDeleteData.close();
      },

      submitService: function() {
        var serviceInfo = this.formServiceInfo
        var basic_service = {
          servicename: serviceInfo.servicename,
          servicecompany: serviceInfo.servicecompany,
          servicetype: serviceInfo.servicetype1,
          testorganization: serviceInfo.testorganization
        };

        CreateServiceInfo(basic_service).then(res => {
          var serviceid_return = res.serviceid

          var detail_service = {
            serviceid: serviceid_return,
            businessscope: serviceInfo.businessscope,
            companyname: serviceInfo.companyname,
            companytype: serviceInfo.companytype,
            ip: serviceInfo.ip,
            legalperson: serviceInfo.legalperson,
            linkman: serviceInfo.linkman,
            linkmantel: serviceInfo.linkmantel,
            password: serviceInfo.password,
            port: serviceInfo.port,
            protocol: serviceInfo.protocol,
            regaddress: serviceInfo.regaddress,
            regcapital: serviceInfo.regcapital,
            regdate: serviceInfo .regdate,
            serviceintro: serviceInfo.serviceintro,
            servicelocation: serviceInfo.servicelocation,
            serviceobject: serviceInfo.serviceobject,
            serviceterm: serviceInfo.serviceterm,
            serviceurl: serviceInfo.serviceurl,
            suic: serviceInfo.suic,
            username: serviceInfo.username,
            term: serviceInfo.term,
            username: serviceInfo.username,
          };
          CreateServiceInfoDetail(detail_service)
            .then(res => {
              var servicedetailid_return = res.servicedetailid
              console.log(servicedetailid_return)
              this.dialogServiceInfoAddVisiable = false
            })
            .catch(err => {
              console.log('err: ', err)
            })

        }).catch(err => {
          console.log('err: ', err)
        })
        this.reload()
        console.log(JSON.stringify(this.formServiceInfo))
      },

      callServiceInfo: function (currentRow) {
        GetServiceInfo()
          .then(async res => {
            const responseData = res.filter(e => e.serviceid == currentRow['serviceid']);
            this.formServiceInfo = responseData[0];
            this.dialogServiceInfoVisiable = true;
          })
          .catch(err => {
            console.log('err: ', err)
          })
      },

      callEvaluation: function (currentRow) {
        let service_evaluation_id = currentRow["serviceid"];
        GetServiceEvaluation(service_evaluation_id)
          .then(async res => {
            let temp = res.filter(e => e.serviceid === service_evaluation_id)[0];
            var index_copy = this.index_base.map(function (value, index) {
              var index_result = temp['result' + (index+1)].split(",")
              var value_1 = JSON.parse(JSON.stringify(value))
              var value_2 = JSON.parse(JSON.stringify(value))
              var value_3 = JSON.parse(JSON.stringify(value))
              var value_4 = JSON.parse(JSON.stringify(value))

              value_1.target_level = 1
              value_2.target_level = 2
              value_3.target_level = 3
              value_4.target_level = 4

              value_1.level_score = index_result[0] + '/' + index_result[1]
              value_2.level_score = index_result[2] + '/' + index_result[3]
              value_3.level_score = index_result[4] + '/' + index_result[5]
              value_4.level_score = index_result[6] + '/' + index_result[7]

              value_1.index_score = index_result[8]
              value_2.index_score = index_result[8]
              value_3.index_score = index_result[8]
              value_4.index_score = index_result[8]
              return [value_1, value_2, value_3, value_4]
            })

            this.formEvaluation = index_copy.flat();
            this.dialogEvaluationVisiable = true;
          })
          .catch(err => {
            console.log('err: ', err)
          })
      },

      callClassification_target: function (currentRow) {
        let service_classification_id = currentRow["serviceid"];
        GetServiceEvaluation(service_classification_id)
          .then(async res => {
            let temp = res.filter(e => e.serviceid === service_classification_id)[0];
            var index_copy = this.index_base.map(function (value, index) {
              var index_result = temp['result' + (index+1)].split(",")
              var value_1 = JSON.parse(JSON.stringify(value))
              var value_2 = JSON.parse(JSON.stringify(value))
              var value_3 = JSON.parse(JSON.stringify(value))
              var value_4 = JSON.parse(JSON.stringify(value))

              value_1.target_level = 1
              value_2.target_level = 2
              value_3.target_level = 3
              value_4.target_level = 4

              value_1.level_score = index_result[0] + '/' + index_result[1]
              value_2.level_score = index_result[2] + '/' + index_result[3]
              value_3.level_score = index_result[4] + '/' + index_result[5]
              value_4.level_score = index_result[6] + '/' + index_result[7]

              value_1.index_score = index_result[8]
              value_2.index_score = index_result[8]
              value_3.index_score = index_result[8]
              value_4.index_score = index_result[8]
              return [value_1, value_2, value_3, value_4]
            })

            this.formServiceClassification_target = index_copy.flat();

            this.dialogClassificationVisiable_target = true;
          })
          .catch(err => {
            console.log('err: ', err)
          })
      },

      callClassification_family: function (currentRow) {
        let service_classification_id = currentRow["serviceid"]
        GetServiceClassification(service_classification_id, 'family')
          .then(async res => {
            let resfilter = res.filter(e => e.serviceid == service_classification_id)[0];
            let responseData = resfilter['23个分类得分的list'].map(function (value, index) {
              var group_result = value.score.split(",")

              var value_1 = JSON.parse(JSON.stringify(value))
              var value_2 = JSON.parse(JSON.stringify(value))
              var value_3 = JSON.parse(JSON.stringify(value))
              var value_4 = JSON.parse(JSON.stringify(value))

              value_1.target_level = 1
              value_2.target_level = 2
              value_3.target_level = 3
              value_4.target_level = 4

              value_1.group_score = group_result[0]
              value_2.group_score = group_result[1]
              value_3.group_score = group_result[2]
              value_4.group_score = group_result[3]

              value_1.level_score = group_result[4]
              value_2.level_score = group_result[4]
              value_3.level_score = group_result[4]
              value_4.level_score = group_result[4]
              return [value_1, value_2, value_3, value_4]
            })

            this.formServiceClassification_family = responseData.flat();
            this.dialogClassificationVisiable_family = true;
          })
          .catch(err => {
            console.log('err: ', err)
          })
      },

      callClassification_sag: function (currentRow) {
        let service_classification_id = currentRow["serviceid"];
        GetServiceClassification(service_classification_id, 'sag')
          .then(async res => {
            let resfilter = res.filter(e => e.serviceid == service_classification_id)[0];
            let responseData = [resfilter['S类'], resfilter['A类'], resfilter['G类']].map(function (value, index) {
              var sag_result = value.score.split(",")

              var value_1 = JSON.parse(JSON.stringify(value))
              var value_2 = JSON.parse(JSON.stringify(value))
              var value_3 = JSON.parse(JSON.stringify(value))
              var value_4 = JSON.parse(JSON.stringify(value))

              var des = value.sagdescription.split(',')

              value_1.sagdescription = des
              value_2.sagdescription = des
              value_3.sagdescription = des
              value_4.sagdescription = des

              value_1.target_level = 1
              value_2.target_level = 2
              value_3.target_level = 3
              value_4.target_level = 4

              value_1.sag_score = sag_result[0]
              value_2.sag_score = sag_result[1]
              value_3.sag_score = sag_result[2]
              value_4.sag_score = sag_result[3]

              value_1.level_score = sag_result[4]
              value_2.level_score = sag_result[4]
              value_3.level_score = sag_result[4]
              value_4.level_score = sag_result[4]
              return [value_1, value_2, value_3, value_4]
            })

            this.formServiceClassification_sag = responseData.flat();
            this.dialogClassificationVisiable_sag = true;
          })
          .catch(err => {
            console.log('err: ', err)
          })
      },

      exportExcel: function () {
        var wb = XLSX.utils.table_to_book(document.querySelector('#formEvaluation'));
        var wbout = XLSX.write(wb, { bookType: 'xlsx', bookSST: true, type: 'array' });
        try {
          FileSaver.saveAs(new Blob([wbout], { type: 'application/octet-stream' }), 'sheetjs.xlsx')
        } catch (e) {
          if (typeof console !== 'undefined')
            console.log(e, wbout)
        }
        return wbout
      },

      spanMethod: function ({ row, column, rowIndex, columnIndex } ) {
        if (columnIndex === 0 || columnIndex === 1 || columnIndex === 2 || columnIndex === 5) {
          if (rowIndex % 4 === 0) {
            return {
              rowspan: 4,
              colspan: 1
            };
          } else {
            return {
              rowspan: 0,
              colspan: 0
            };
          }
        }
      },

      spanMethod_classification_1: function ({ row, column, rowIndex, columnIndex } ) {
        if (columnIndex === 0 || columnIndex === 1 || columnIndex === 4) {
          if (rowIndex % 4 === 0) {
            return {
              rowspan: 4,
              colspan: 1
            };
          } else {
            return {
              rowspan: 0,
              colspan: 0
            };
          }
        }
      },

      spanMethod_classification_2: function ({ row, column, rowIndex, columnIndex } ) {
        if (columnIndex === 0 || columnIndex === 3) {
          if (rowIndex % 4 === 0) {
            return {
              rowspan: 4,
              colspan: 1
            };
          } else {
            return {
              rowspan: 0,
              colspan: 0
            };
          }
        }
      },

      spanMethod_classification_3: function ({ row, column, rowIndex, columnIndex } ) {
        if (columnIndex === 0 || columnIndex === 3) {
          if (rowIndex % 4 === 0) {
            return {
              rowspan: 4,
              colspan: 1
            };
          } else {
            return {
              rowspan: 0,
              colspan: 0
            };
          }
        } else {
          if (columnIndex === 2 || columnIndex === 3) {
            if (rowIndex % 4 === 0) {

            }
          }
        }
      }
    }
  }
</script>


