import request from '@/plugin/axios'

export function CreateServiceClassification (data) {
  return request({
    url: '/demo/service_classification/create',
    method: 'post',
    data
  })
}

export function RetrieveServiceClassification (data) {
  return request({
    url: '/demo/service_classification/retrieve',
    method: 'post',
    data
  })
}

