// remote url
export function GetServiceClassification(data, param) {
  if(param === 'target'){
    return request({
      url: '/issue1/index/getAll/',
      method: 'get'
    });
  }
  else if(param === 'family'){
    return request({
      url: '/issue1/groupLevel/getAllDto',
      method: 'get'
    });
  }
  else if(param === 'sag'){
    return request({
      url: '/issue1/sagLevel/getAllDto',
      method: 'get'
    });
  }
  else {
    console.log('Do Nothing');
  }
}

export function UpdateServiceClassification (data) {
  return request({
    url: '/demo/service_classification/update',
    method: 'post',
    data
  })
}

export function DeleteServiceClassification (data) {
  return request({
    url: '/demo/service_classification/delete',
    method: 'post',
    data
  })
}
