export function CreateServiceInfo (data) {
  return request({
    url: '/issue1/service/add',
    method: 'post',
    data
  })
}


export function CreateServiceInfoDetail (data) {
  return request({
    url: '/issue1/serviceDetail/add',
    method: 'post',
    data
  })
}

export function RetrieveServiceInfo (data) {
  return request({
    url: '/demo/service_info/retrieve',
    method: 'post',
    data
  })
}

