// remote url
export function GetServiceInfo () {
  return request({
    url: '/issue1/serviceDetail/getAll/',
    method: 'get'
  })
}

export function UpdateServiceInfo (data) {
  return request({
    url: '/demo/service_info/update',
    method: 'post',
    data
  })
}

